const container = document.getElementById('root'); // find root tag
const ajax = new XMLHttpRequest(); // ajax Ï∂úÎ†• Í≤∞Í≥º Î∞òÌôò
const content = document.createElement('div');
const URL_ADDR = 'https://api.hnpwa.com/v0/news/1.json';
const CONTENT_URL = 'https://api.hnpwa.com/v0/item/@id.json'; // Ìï¥Îãπ ÏΩòÌÖêÏ∏†Ïùò url

// Í≥µÏú† ÏûêÏõê Í¥ÄÎ¶¨
const store = {
    currentPage : 1,
};

function getData(method, url, async) {
    ajax.open(method, url, async); // ÎèôÍ∏∞ or ÎπÑÎèôÍ∏∞ Î∞©ÏãùÏúºÎ°ú ÏÑúÎ≤Ñ ÏöîÏ≤≠ Í∞í Ï≤òÎ¶¨
    ajax.send(); // Îç∞Ïù¥ÌÑ∞Î•º Í∞ÄÏ†∏Ïò§Îäî ÏûëÏóÖ

    return JSON.parse(ajax.response);
}

function getNewsFeed() {
    const newsFeed = getData('GET', URL_ADDR, false); // json Îç∞Ïù¥ÌÑ∞ Í∞ùÏ≤¥ Î≥ÄÌôò ÌõÑ Î¶¨ÌÑ¥
    const newsList = []; // empty array
    let template = `
        <div class="bg-gray-600 min-h-screen">
            <div class="bg-white text-xl">
                <div class="mx-auto px-4">
                    <div class="flex justify-between items-center py-6">
                        <div class="flex justify-start">
                            <h1 class="font-extrabold">Hacker News</h1>
                        </div>
                        <div class="item-center justify-end">
                            <a href="#/page/{{__prev_page__}}" class="text-gray-500">Previous</a>
                            <a href="#/page/{{__next_page__}}" class="text-gray-500 ml-4">Next</a>
                        </div>
                    </div>
                </div>
            </div>
            <div class="p-4 text-2xl text-gray-700">{{__news_feed__}}</div>
        </div>
    `;
    
    for (let i = (store.currentPage - 1) * 10; i < store.currentPage * 10; i++) {
        newsList.push(
        `
        <div class="p-6 bg-white mt-6 rounded-lg shadow-md transition-colors duration-500 hover:bg-green-100">
            <div class="flex">
                <div class="flex-auto">
                    <a href="#/show/${newsFeed[i].id}">${newsFeed[i].title}</a>
                </div>
                <div class="text-center text-sm">
                    <div class="w-10 text-white bg-green-300 rounded-lg px-0 py-2">
                        ${newsFeed[i].comments_count}
                    </div>
                </div>
            </div>
            <div class="flex mt-3">
                <div class="grid gird-cols-3 text-sm text-gray-500">
                    <div><i class="fas fa-user mr-1"></i>${newsFeed[i].user}</div>
                    <div><i class="fas fa-heart mr-1"></i>${newsFeed[i].points}</div>
                    <div><i class="far fa-clock mr-1"></i>${newsFeed[i].time_ago}</div>
                </div>
            </div>
        </div>
        `);
    }

    template = template.replace('{{__news_feed__}}', newsList.join('')); // template replace - news list content
    template = template.replace('{{__prev_page__}}', store.currentPage > 1 ? store.currentPage - 1 : 1); // prev page 
    template = template.replace('{{__next_page__}}', store.currentPage + 1); // next page
    
    container.innerHTML = template; 
}

function newsDetail() {
    console.log('hash changed')
    console.log(location.hash); // location Í∞ùÏ≤¥Ïùò hash Í∞í ÌôïÏù∏ #3029303929 ÏôÄ Í∞ôÏùÄ Î∞©ÏãùÏúºÎ°ú Í∞í Î∞òÌôò

    const id = location.hash.substr(7); // # Ïù¥ÌõÑÏùò ÎÇ¥Ïö© Ï†ÄÏû•
    const newsContent = getData('GET', CONTENT_URL.replace('@id', id), false);

    container.innerHTML = `
        <h1>${newsContent.title}</h1>

        <div>
            <a href="#/page/${store.currentPage}">Î™©Î°ùÏúºÎ°ú</a>
        </div>
    `;
}

function router() {
    const routePath = location.hash;

    if(routePath === '') {
        // location.hash === # Ïùº Í≤ΩÏö∞ Îπà Í∞í Î∞òÌôò
        getNewsFeed();
    }
    else if (routePath.indexOf('#/page/') >= 0) {
        store.currentPage = Number(routePath.substr(7));
        getNewsFeed();
    }
    else {
        newsDetail();
    }
}

window.addEventListener('hashchange', router); // router : hashÏùò Î≥ÄÍ≤ΩÎßàÎã§ Î≥¥Ïó¨Ï§å
router();